<style scoped>
.login {
  width: 432px;
  margin: 100px auto 0;
}
</style>


<template>
  <div  >
    <Header/>
    <div class="login">
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm"   label-width="100px" class="demo-ruleForm">
      <el-form-item label="邮箱啊" prop="email">
        <el-input  v-model="ruleForm.email" autocomplete="off" ></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input type="password" v-model="ruleForm.password"  autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
        <!-- <el-button @click="$router.push({name:'register'})">注册</el-button> -->
      </el-form-item>
    </el-form>
  </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import Header from "@/components/dmb_header"
export default {
  components:{
    Header
  },
  data() {
    return {
      ruleForm: {
        email: "",
        password: ""
      },
      rules: {
        email: [
          { required: true, message: "请输入邮箱地址", trigger: "blur" },
          {
            type: "email",
            message: "请输入正确的邮箱地址",
            trigger: ["blur", "change"]
          }
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }]
      }
    };
  },
  created() {
    if (this.userInfo) this.$router.push({ name: "index" });
  },
  methods: {
    async login() {
      let { data } = await axios.post("/login", this.ruleForm);
      if (data.status == 0) {
        this.$message.success("登录成功");
        this.tool.setLocal("dmbID", data.data.token, 60 * 60 * 24 * 7);
        this.$router.push({ name: "space" });
      } else if (data.status == 1) {
        this.$message.error(data.message);
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.login();
        } else {
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  },
  computed: {
    ...mapState(["userInfo"])
  },
    beforeRouteEnter(to, from, next) {
			next(vm => {
				if(!vm.tool.getLocal('dmbID')) { 
					next()
				} else {
					next({
						name: "space"
					})
				}
			})
		}
};
</script>

