<style scoped>
.header {
  height: 80px;
  line-height: 80px;
}
.header-inner {
  height: 80px;
}
.logo {
  float: left;
}
.nav {
  float: right;
}
.nav li {
  cursor: pointer;
  float: right;
  margin:0 10px;
}
</style>
<template>
  <div class="header">
    <div class="header-inner w1160">
      <div class="logo">
        <router-link to="/" title="动漫部">
          <img src="../assets/images/logo.png" alt="动漫部">
        </router-link>
      </div>
      <ul class="nav">
        <li v-if="!userInfo">
          <router-link :to="{name:'login'}" style="font-size:12px;color:red" title="入部"> 入部</router-link>
        </li>
        <li v-else>
          <router-link :to="{name:'space'}" style="font-size:12px;color:red">{{userInfo.email}}</router-link>
          <i @click="logout" style="font-size:12px;color:red" title="注销"> 注销</i>
        </li>
        <li>
       
            <img src="../assets/images/header_nav_dch.png" style="position: relative; top: -1px;" alt="请给我一碗蛋炒饭">
      
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  created() {
    if (this.tool.getLocal("dmbID")) {
      this.getUserInfo();
    }
  },
  methods: {
    async getUserInfo() {
      let user = await axios.get("/getUserInfo");
      if (user.data.data) {
        this.$store.commit("setUserInfo", user.data.data);
      }
    },
    async logout() {
      this.tool.removeLocal("dmbID");
      this.$store.commit("setUserInfo", null);
      this.$router.push({ name: "index" });
    }
  },
  computed: {
    ...mapState(["userInfo"])
  }
};
</script>


